<template>
  <div id="settings" class="card align-self-center">
    <div class="card-body">
      <h1 class="text-center align-self-start">Timer</h1>
      <div class="row">
        <div class="col form-group">
          <label for="minutes">Minutes</label>
          <select id="minutes" v-model="min" class="form-control" >
            <option v-for="m in 11">{{ m - 1 }} </option>
          </select>
        </div>
        <div class="col form-group">
          <label for="seconds">Seconds</label>
          <select id="seconds"  v-model="sec" class="form-control" >
            <option v-for="s in 12">{{ (s-1) * 5 }} </option>
          </select>
        </div>
      </div>
      <div>
        <button v-on:click="runTimer" class="btn btn-primary btn-lg btn-block">Start</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'settings',
  props: ['minutes', 'seconds'],
  mounted: function () {
    var stored = localStorage.getItem('time')
    if (stored) {
      var time = JSON.parse(stored)
      if (time) {
        this.sec = time.seconds
        this.min = time.minutes
        return
      }
    }

    this.sec = this.seconds
    this.min = this.minutes
  },
  methods: {
    runTimer: function () {
      var time = {
        minutes: this.min,
        seconds: this.sec
      }
      localStorage.setItem('time', JSON.stringify(time))

      this.$emit('runTimer', time)
    }
  },
  data () {
    return {
      min: 1,
      sec: 0
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #settings {
    max-width: 500px;
    margin:0 auto;
  }
  label {
    color:#ADCAD1;
  }
</style>



