<template>
  <div>
    <div ref="canvas-container" class="text-center">
      <canvas ref="canvas" width="400" height="400"></canvas>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'

export default {
  name: 'settings',
  props: ['progress', 'running'],
  mounted: function () {
    this.canvas = this.$refs.canvas
    if (this.canvas) {
      this.resize()
    }
  },
  created: function () {
    window.addEventListener('resize', this.resize.bind(this))
  },
  updated: function () {
  },
  methods: {
    resize: function () {
      var container = this.$refs['canvas-container']
      if (!container) {
        return
      }
      var w = container.clientWidth
      if (w > 400) {
        w = 400
      }
      this.canvas.width = w * 2
      this.canvas.height = w * 2
      this.canvas.style.width = w + 'px'
      this.canvas.style.height = w + 'px'

      this.ctx = this.canvas.getContext('2d')
      this.ctx.scale(2, 2)
    }
  },
  data () {
    return {
      draw: function () {
        var w = this.canvas.width / 2
        var h = this.canvas.height / 2
        var r = h / 2
        var l = 4

        var border = new Path2D()
        border.arc(w / 2, h / 2, r - (l / 2), 0, 2 * Math.PI)

        var circle = new Path2D()
        circle.moveTo(w / 2, h / 2)
        circle.arc(w / 2, h / 2, r - (l / 2), -Math.PI / 2, 2 * Math.PI * this.progress - Math.PI / 2)

        this.ctx.clearRect(0, 0, w, h)

        this.ctx.fillStyle = '#ffffff'
        this.ctx.fill(border)

        if (this.progress === 1) {
          this.ctx.fillStyle = '#AA4639'
        } else {
          this.ctx.fillStyle = '#0F434F'
        }
        this.ctx.fill(circle)

        this.ctx.lineWidth = l
        this.ctx.strokeStyle = '#ADCAD1'
        this.ctx.stroke(border)
      },
      update: function () {
        this.frame = requestAnimationFrame(this.update.bind(this))
        this.draw()
      },
      start: function () {
        Vue.nextTick()
          .then(function () {
            this.canvas = this.$refs.canvas
            this.update()
          }.bind(this))
      },
      stop: function () {
        this.draw()
        cancelAnimationFrame(this.frame)
      }
    }
  },
  watch: {
    running: {
      immediate: true,
      handler: function () {
        if (this.running) {
          this.start()
        } else {
          this.stop()
        }
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



