<template>
  <div id="timer">
    <div class="d-flex" id="timer-container">
      <transition appear name="fade" mode="out-in">
        <settings  key="settings" v-if="!showTimer" :minutes="minutes" :seconds="seconds" v-on:runTimer="runTimer"></settings>
        <div  class="card align-self-center" id="graph-holder" v-if="showTimer">
          <div class="card-body">
            <a v-on:click="stopTimer" href="#" class="close">&times;</a>
            <graph :progress="progress" :running="running"></graph>
            <h2 class="text-center">
              <span> {{ m | zerofill }} </span>:
              <span> {{ s | zerofill }} </span>
            </h2>
            <div class="row justify-content-between align-items-center">
              <div class="col-3">
                <button v-on:click="removeTime" class="btn btn-light btn-sm btn-block">-{{ step / 1000 }}s</button>
              </div>
              <div class="col-6">
                <button v-if="running" v-on:click="pauseTimer" class="btn btn-danger btn-lg btn-block">Pause</button>
                <button v-if="!running" v-on:click="startTimer" class="btn btn-primary btn-lg btn-block">Start</button>
              </div>
              <div class="col-3">
                <button v-on:click="addTime" class="btn  btn-light btn-sm btn-block">+{{ step / 1000 }}s</button>
              </div>
            </div>
          </div>

        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import moment from 'moment'
import Settings from '@/components/Settings'
import Graph from '@/components/Graph'

export default {
  name: 'Timer',
  mounted: function () {
    // this.$on('runTimer', function() {
    //   console.log('xxxx')
    // })
  },
  components: {
    'settings': Settings,
    'graph': Graph
  },
  methods: {
    update: function () {
      this.frame = requestAnimationFrame(this.update.bind(this))
      var t = this.totalTime - (this.currentTime + moment().diff(this.startTime)) + 1000
      if (t < 0) {
        t = 0
      }
      // console.log(now.diff(this.startTime))
      this.m = Math.floor((t / 1000 / 60) % 60)
      this.s = Math.floor((t / 1000) % 60)
      this.tempTime = (this.currentTime + moment().diff(this.startTime))
      this.progress = this.tempTime / this.totalTime

      if (this.tempTime >= this.totalTime) {
        this.s = 0
        this.m = 0
        this.progress = 1
        this.running = false
        this.finished = true
        cancelAnimationFrame(this.frame)
      }
    },
    startTimer: function () {
      if (this.finished) {
        this.currentTime = 0
        this.tempTime = 0
        this.finished = false
      }
      this.running = true
      this.startTime = moment()
      this.currentTime = this.tempTime
      // this.endTime = moment().add(self.seconds, 'seconds').add(self.minutes, 'minutes');
      this.update()
    },
    stopTimer: function () {
      this.pauseTimer()
      this.showTimer = false
    },
    pauseTimer: function () {
      this.running = false
      cancelAnimationFrame(this.frame)
    },
    runTimer: function (time) {
      this.minutes = time.minutes
      this.seconds = time.seconds

      this.showTimer = true
      this.totalTime = this.minutes * 60 * 1000 + this.seconds * 1000
      this.step = Math.round(this.totalTime / 10)
      this.step = Math.ceil((this.step + 1) / 5000) * 5000

      this.currentTime = 0
      this.tempTime = 0

      this.startTimer()
    },
    addTime: function () {
      this.currentTime += this.step
    },
    removeTime: function () {
      this.currentTime -= this.step
      if (this.currentTime < 0) {
        this.currentTime = -moment().diff(this.startTime)
      }
    }
  },
  data () {
    return {
      finished: true,
      progress: 0,
      m: 0,
      s: 0,
      minutes: 1,
      seconds: 0,
      running: false,
      step: 6,
      showTimer: false
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.fade-enter-active, .fade-leave-active {
  transition: opacity .3s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

#graph-holder {
  margin: 0 auto;
}
#timer-container,
#timer {
  height: 100%;
}
.close {
  font-size: 2.5rem;
}
</style>



